import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { QuestionFormComponent } from '../question-form/question-form.component';

@Component({
  selector: 'app-help-component',
  templateUrl: './help-component.component.html',
  styleUrls: ['./help-component.component.scss']
})
export class HelpComponentComponent implements OnInit {

  constructor(private dialog: MatDialog) { }

  ngOnInit(): void {
  }
  OpenQuestionModal(){
       
    const dialogRef: MatDialogRef<QuestionFormComponent> = this.dialog.open(QuestionFormComponent, {
      width: '',
      height: '',
      data: {},
      panelClass: 'custom-dialog-AddQuery-class',
      disableClose:true,
      // position: {
      //   center: '0',
      // },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });  

  }

  }


