import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HelpComponentComponent } from './help-component/help-component.component';
import { SaleQuestionComponent } from './sale-question/sale-question.component';
import { PrchaseQuestionComponent } from './prchase-question/prchase-question.component';
import { ProductQuestionComponent } from './product-question/product-question.component';

const routes: Routes = [
  { 
    path: '', 
    redirectTo: 'help-faq',
    pathMatch: 'full'
  },
  {
    path:"help-faq",
    component:HelpComponentComponent,
    children:[
      
    {path: '', 
      redirectTo: 'sale-faq',
      pathMatch: 'full'
  },
      {
        path:"sale-faq",
        component:SaleQuestionComponent
      },
      {
        path:"purchase-faq",
        component:PrchaseQuestionComponent
      },
      {
        path:"product-faq",
        component:ProductQuestionComponent
      },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpAnFaqModuleRoutingModule { }
