import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ContactUsServiceService } from 'src/app/core/services/contact-us-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-question-form',
  templateUrl: './question-form.component.html',
  styleUrls: ['./question-form.component.scss']
})
export class QuestionFormComponent implements OnInit {
  public conactForm: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))

  constructor(private toastService:ToastNotificationService,
    private ContactUsServiceService:ContactUsServiceService, private titleCasePipe: TitleCasePipe, 
    private router: Router,private formBuilder: FormBuilder,public dialogRef: MatDialogRef<QuestionFormComponent>) { }

  ngOnInit(): void {
    this.createQuery()
  }
  createQuery(){
    this.conactForm = this.formBuilder.group({
      ticketType:[],
      Message:[""],
    });
  }
  cancel(){
    this.dialogRef.close();
  }
  postQuery(){

    let body=
{
  
  "enquiry":{
    "ticketType":this.conactForm.controls.ticketType.value,
    "description":this.conactForm.controls.Message.value,
    "image":""
  },

  "enquiryImages":[],
}
    if (this.conactForm.valid) {
      this.ContactUsServiceService.contactUspost(body,this.currentUser.id).then((res)=>{
        if (res) {
          this.toastService.openSnackBar("Ticket Raised Successfully !!!");
          this.router.navigate(["/pages/contactlist-page"]);
          this.dialogRef.close();
        }
       
      },(err) => {
        if(err.error.expose){
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else{
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }      
      })  
    }
  }
 
}
